@import "../variables";
@import "../mixins";

.nav {
	display: flex;
	list-style: none;
	padding: 0;
	margin: 0;
}

.nav--product {
	display: flex;
	justify-content: flex-start;
	width: 100%;
	background: #faf7f7;

	&>li {
		flex: 1 1 auto;

		&>a,
		&>span {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			min-height: 80px;
			z-index: 1;
			font-size: 15px;
			font-weight: 400;
			text-transform: uppercase;
			background: #faf7f7;
			color: #a39296;
		}

		&:hover,
		&:active,
		&.active {

			&>a,
			&>span {
				background: #b92442;
				color: #f9f5f5;
			}
		}

	}

	@include media-breakpoint-down(lg) {}

	@include media-breakpoint-down(md) {}
}
